* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.info {
  display: flex;
  align-items: center;
}

.img-info {
  width: 40px;
  margin-right: 10px;
}

.country-img {
  width: 25px;
  margin-right: 5px;
}

.country-item {
  display: flex;
  align-items: center;
}
